
.am-checkbox {
  display: inline-block;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 0;
  padding-right: 0.5rem;
}
.am-checkbox + .am-checkbox {
  margin-top: 0;
  margin-bottom: 0;
}
.am-checkbox .am-ucheck-checkbox {
  margin: 0 !important;
}
.am-checkbox .am-ucheck-checkbox,
.am-checkbox .am-ucheck-icons {
  margin: auto;
  top: 0;
  bottom: 0;
}
.am-checkbox.am-disabled {
  color: #eeeeee;
}
.am-checkbox.am-disabled .am-ucheck-icons {
  color: #eeeeee !important;
}



